body {
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

h2 {
  text-align: center;
  margin-bottom: 20px;
}

#login-form {
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 400px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

input[type="email"], input[type="password"] {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 15px;
  padding: 10px;
  font-size: 16px;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 5px;
  width: 100%;
  padding: 12px;
  font-size: 16px;
}

button:hover {
  background-color: #0056b3;
}

#habit-section {
  background-color: #fff;
  border-radius: 8px;
  width: 100%;
  max-width: 600px;
  margin: 50px auto;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

ul#habits {
  padding: 0;
  list-style-type: none;
}

ul#habits li {
  background-color: #f9f9f9;
  border-radius: 4px;
  justify-content: space-between;
  margin-bottom: 8px;
  padding: 10px;
  display: flex;
}

ul#habits li button {
  color: #fff;
  background-color: red;
  border: none;
  border-radius: 3px;
  padding: 5px;
}

ul#habits li button:hover {
  background-color: #e60000;
}

input#habit-name {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 20px;
  padding: 10px;
}
